import React, {Component} from 'react';
import '../../css/ui/Tools.scss';
export default class Tools extends Component {
  constructor(props){
    super(props);
    this.state = {
      info : true
    }
  }

  componentDidMount(){
    // console.log('Tools UI Mount');
  }
  componentDidUpdate(){
    // console.log('Tools UI Update');
  }
  componentWillUnmount(){
    // console.log('Tools UI Unmount');
  }
  addImage = () => {
    this.props.addImage('https://source.unsplash.com/random/500x400');
  }

  handleInfo = () => {
    this.setState({info : !this.state.info});
  }

  render(){
    return (
      <div className="sub">
        <div className="sub-title">
            Tools
        </div>
        <div className="sub-tools-menu">
          <div>
              <button onClick={this.props.objectInfo}>  </button>
              <button onClick={this.props.getCanvasInfo}> </button>
              <button onClick={this.props.getCanvasBackinfo}>  </button>
          </div>
          <div>
              <button onClick={this.props.openSaveModal}>save</button>
          </div>
          
          <button onClick = {this.handleInfo} style={{color : 'black', backgroundColor : 'skyblue'}}>  </button>
          { this.state.info ? 
            <div>
              <div style={ {color : 'red', padding : '10px'}}>
              {this.props.object.type !== 'not active' ? 
                <div>
                  <p>object left : {this.props.object.left}</p>
                  <p>object top : {this.props.object.top}</p>
                  <p>object scaleX : {this.props.object.scaleX}</p>
                  <p>object scaleY : {this.props.object.scaleY}</p>

                  <p> left : {this.props.object.aCoords.tl.x} </p>
                  <p> right : {this.props.object.aCoords.br.x} </p>
                  <p> top : {this.props.object.aCoords.tl.y} </p>
                  <p> bottom : {this.props.object.aCoords.br.y} </p>
                </div> : null}
              </div>

              <div style={ { padding : '10px'}}>
              {this.props.canvas ? 
                <div>
                  <p>    = x : {(-this.props.canvasView.x / this.props.zoom)}  y : {(-this.props.canvasView.y / this.props.zoom)}</p>
                  <p>    = x : { (-this.props.canvasView.x / this.props.zoom)  + (this.props.canvas.width / this.props.zoom) }  
                      y : {(-this.props.canvasView.y / this.props.zoom) + (this.props.canvas.height / this.props.zoom) }</p>

                  {this.props.canvas.backgroundImage ?
                <div>
                  <p> width {this.props.canvas.backgroundImage.width}</p>
                  <p> height {this.props.canvas.backgroundImage.height}</p>
                  <p> scaleX {this.props.canvas.backgroundImage.scaleX}</p>
                  <p> scaleY {this.props.canvas.backgroundImage.scaleY}</p>
                  <p> left {this.props.canvas.backgroundImage.left}</p>
                  <p> top {this.props.canvas.backgroundImage.top}</p>
                </div> : null}
              </div> : null}
            </div>
          </div> : null   
        }


          <div>
            <p>  : {this.props.user_name} </p>
            <p>  : {this.props.prj_idx} </p>
            <p> : {this.props.isSaved ? 'true' : 'false'} </p>           
          </div>
        </div>
      </div>
    );
  }

}